import React, { useState, useEffect } from 'react'
import clsx from 'clsx'

import Cache from '../../controller/Cache'
import i18n from '../../controller/i18n'
import Events from '../../controller/Events'
import Notifications from '../../controller/Notifications'
import Settings from '../../controller/Settings'

import { Alert, AlertTitle } from '@material-ui/lab'
import IconButton from '@material-ui/core/IconButton'
import Snackbar from '@material-ui/core/Snackbar'
import Typography from '@material-ui/core/Typography'
import DeleteIcon from '@material-ui/icons/Delete'
import CheckIcon from '@material-ui/icons/Check'
import CloseIcon from '@material-ui/icons/Close'

import Dialog from '../../components/Dialog'
import Icon from '../../components/Icon'
import Inputs from '../../components/Inputs'
import LinearProgress from '../../components/Progress'


/*
 * STYLES
 */
import { emphasize, makeStyles } from '@material-ui/core/styles';
const useStyles = makeStyles(theme => {
	const emphasis = theme.palette.type === 'light' ? 0.8 : 0.98;
	const backgroundColor = emphasize(theme.palette.background.default, emphasis);
	
	return {
		AlertNone: styles => ({
			color: styles.color || theme.palette.getContrastText(backgroundColor),
			backgroundColor: styles.backgroundColor || backgroundColor,
		}),
		LinearProgressDialog: {
			margin: theme.spacing(2, -7, -2, -3)
		},
		LinearProgressSnackbar: {
			marginTop: -4
		},
		NotificationSnackbar: {
			display: 'inherit'
		},
		NotificationPopup: styles => ({
			backgroundColor: styles.backgroundColor,
			color: styles.color
		})
	}
});


export default function NotificationEvent(props) {
	const NotificationsClass = new Notifications();
	
	const [notification,setNotification] = useState(null);
	const onClose = () => setNotification(null);
	const onDelete = () => {
		NotificationsClass.del(notification.id);
		onClose();
	}
	
	useEffect(() => {
		
		// listen for new notifications
		NotificationsClass.on('new', item => {
			
			// mark as read
			if (!item.state || item.state === 'unread') {
				NotificationsClass.set(item.id, { 'state': 'read' });
			}
			
			// change view
			if (item && item.focusTab) {
				const tabs = (Settings.getLayout()[0] && Settings.getLayout()[0].tabs) || [];
				const focusTab = item.focusTab.indexOf('-') > -1 ? item.focusTab.substr(0, item.focusTab.indexOf('-')) : item.focusTab;
				
				const events = new Events();
				events.emit('tabs.navigate', tabs[tabs.findIndex(tab => tab.title.toLowerCase().replace(/ /g, '') === focusTab.toLowerCase())]);
			}
			
			// apply callback
			if (item && item.callback) {
				item.callback();
			}
			
			//
			setNotification(item);
		});
		
		// ask for browser subscription (either when 1. not asked before OR 2. confirm before but browser status differs
		if (Cache.get('askBrowserSubscription') === null || (Cache.get('askBrowserSubscription') === true && NotificationsClass.getBrowserSubscription() !== true)) {
			NotificationsClass.askBrowserSubscription();
		}
		
	}, [NotificationsClass]);
	
	
	// show Snackbar or Popup
	if (notification && (!notification.display || notification.display === 'modal' || notification.display === 'popup')) {
		return <NotificationPopup onClose={onClose} onDelete={onDelete} {...notification} />
	}
	else if (notification && (notification.display === 'snackbar' || notification.display === 'alert')) {
		return <NotificationSnackbar onClose={onClose} onDelete={onDelete} {...notification} />
	}
	
	// Browser Notification subscription
	else if (notification && notification.display === 'askBrowserSubscription') {
		return <BrowserNotificationSubscription onClose={onClose} {...notification} />
	}
	
	return null;
}

export function BrowserNotificationSubscription(props) {
	const NotificationsClass = new Notifications();
	
	const onConfirm = () => {
		NotificationsClass.reqBrowserSubscription();
		
		Cache.set('askBrowserSubscription', true);
		props.onClose();
	}
	
	const onCancel = () => {
		Cache.set('askBrowserSubscription', false);
		props.onClose();
	}
	
	
	return (
		<NotificationSnackbar
			criticality="info"
			snackbarOrigin={{ vertical: 'bottom', horizontal: 'right' }}
			title={i18n.t('Use Browser Notification Feature')}
			message={i18n.t('Do you want notifications to be pushed to the browser?')}
			autoHideDuration={10000}
			{...props}
			
			onClose={onCancel}
			action={<>
				<IconButton
					aria-label="confirm"
					color="inherit"
					size="small"
					onClick={onConfirm}
					>
					
					<CheckIcon fontSize="inherit" />
				</IconButton>
				<IconButton
					aria-label="close"
					color="inherit"
					size="small"
					onClick={onCancel}
					>
					
					<CloseIcon fontSize="inherit" />
				</IconButton>
			</>}
			/>
	);
}

export function NotificationPopup(props) {
	const { onClose, onDelete, id, icon = null, title, message, autoHideDuration = null } = props; // criticality = "none", 
	const classes = useStyles({ 'color': props.fontColor, 'backgroundColor': props.backgroundColor });
	
	return (
		<Dialog
			classes={{ CustomDialogContent: classes.NotificationPopup }}
			key={'notification-' + id}
			id='NotificationPopup'
			
			open={true}
			onClose={onClose}
			
			header={message && (
				<>
					{icon && <Icon icon={icon} color={props.iconColor} style={{ marginRight: 8 }} />}
					<Typography className={classes.DialogTitleTypography} display="inline" variant="h6">
						{title}
					</Typography>
					
					{autoHideDuration !== null && (
					<LinearProgress
						className={classes.LinearProgressDialog}
						total={autoHideDuration}
						onCompleted={onClose}
						/>)}
				</>
			)}
			actions={<>
				<Inputs.Button key='action-delete' variant="text" onClick={onDelete} label="Delete" startIcon={<DeleteIcon />} gapRight color="error" />
				<Inputs.Button key='action-close' variant="text" onClick={onClose} label="Close" />
			</>}
			>
			
			<div dangerouslySetInnerHTML={{ __html: message || title }} />
		</Dialog>
	);
}

export function NotificationSnackbar(props) {
	const { onClose, id, icon = null, title, message, criticality = "none", snackbarOrigin = {}, autoHideDuration = null, action } = props;
	const classes = useStyles({ 'color': props.fontColor, 'backgroundColor': props.backgroundColor });
	
	let AlertProps = {}
	
	// custom icon
	if (icon) {
		AlertProps.icon = <Icon icon={icon} color={props.iconColor} />
	}
	
	// custom action
	if (action) {
		AlertProps.action = action;
	}
	
	return (
		<Snackbar
			classes={{ root: classes.NotificationSnackbar }}
			key={'notification-' + id}
			open={true}
			anchorOrigin={{ vertical: snackbarOrigin.vertical || 'bottom', horizontal: snackbarOrigin.horizontal || 'center' }}
			//autoHideDuration={autoHideDuration}
			onClose={onClose}
			>
			
			<>
				<Alert
					classes={{ root: clsx(['error', 'warning', 'info', 'success'].indexOf(criticality) === -1 && classes.AlertNone) }}
					elevation={6}
					variant="filled"
					onClose={onClose && (() => onClose())}
					severity={['error', 'warning', 'info', 'success'].indexOf(criticality) === -1 ? 'info' : criticality}
					{...AlertProps}
					>
					
					<AlertTitle>{title}</AlertTitle>
					{message}
				</Alert>
				{autoHideDuration !== null && (
				<LinearProgress
					className={classes.LinearProgressSnackbar}
					total={autoHideDuration}
					onCompleted={onClose}
					/>)}
			</>
			
		</Snackbar>
	);
}